import React from 'react';
import Container from '../component/container';
import Lan from '../modules/language';
import Data from '../data/post';
import PostCard from '../component/post-card';

export default class extends React.Component {
    state = {
        page: 1,
        totalPage: 1,
        data: [],
    }

    componentDidMount() {
        const result = Data.getLatestPosts(1);
        const posts = result.data.map(item => PostCard(item))
        const list = <div key={1} className="col-12 row mx-0">{posts}</div>

        const move = [];
        if (this.state.page > 1) {
            move.push(<button onclick={()=>this.goToPrePage()} key={1}>{Lan.t('previous_page')}</button>)
        }
        if (this.state.page < result.totalPage) {
            move.push(<button onclick={()=>this.goToNextPage()} key={2}>{Lan.t('next_page')}</button>)
        }
        const moveView = <div key={2} className="col-12 d-flex justify-content-center row mx-0 mb-5">{move}</div>
        // console.log(posts);
        this.setState({ data: [list, moveView], totalPage: result.totalPage });
    }

    render() {
        return (
            <Container
                title={Lan.t('blog_name')}>
                {this.state.data}
            </Container>

        );
    }
}